.container {
  max-width: 480px;
  margin: 0 auto;
  padding: 0 15px;
}

@media (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1100px;
  }
}

.header__nav {
  justify-content: space-between;
  align-items: center;
  font-size: 18px;
  font-weight: 600;
  line-height: 2.22;
  display: flex;
}

@media (max-width: 300px) {
  .header__nav {
    flex-direction: column;
    gap: 20px;
  }
}

.logo {
  display: block;
}

.logo__img {
  max-width: auto;
  height: 50px;
}

.mobile-menu {
  cursor: pointer;
  background-color: #333;
  padding: 10px;
  display: block;
}

.menu {
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
}

@media (min-width: 768px) {
  .menu {
    justify-content: center;
    align-items: center;
    gap: 24px;
    display: flex;
  }
}

.menu__link {
  padding: 5px 0;
  transition: color .5s, text-shadow .5s;
  display: block;
}

.menu__link:hover {
  color: #4654a3;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, .5);
}

.row {
  position: relative;
}

@media (min-width: 768px) {
  .row {
    display: none;
  }
}

#hamburg:checked + .hamburg + nav.topmenu {
  max-height: 600px;
}

label.hamburg {
  width: 75px;
  height: 50px;
  cursor: pointer;
  background: #4654a3;
  border-radius: 4px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

input#hamburg {
  display: none;
}

.line {
  height: 4px;
  width: 55px;
  transform-origin: center;
  background: #fff;
  border-radius: 2px;
  transition: all .5s;
  display: block;
  position: absolute;
  left: 10px;
}

.line:nth-child(1) {
  top: 12px;
}

.line:nth-child(2) {
  top: 24px;
}

.line:nth-child(3) {
  top: 36px;
}

#hamburg:checked + .hamburg .line:nth-child(1) {
  transform: translateY(12px)rotate(-45deg);
}

#hamburg:checked + .hamburg .line:nth-child(2) {
  opacity: 0;
}

#hamburg:checked + .hamburg .line:nth-child(3) {
  transform: translateY(-12px)rotate(45deg);
}

.topmenu {
  z-index: 1;
  width: 480px;
  height: auto;
  max-height: 0;
  opacity: .9;
  border-radius: 4px;
  transition: all .5s;
  position: fixed;
  top: 70px;
  left: 50%;
  overflow: hidden;
  transform: translateX(-50%);
}

@media (max-width: 299.98px) {
  .topmenu {
    top: 140px;
    left: 50%;
  }
}

.topmenu__list {
  background-color: #4654a3;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  font-size: 24px;
  display: flex;
}

.topmenu__item {
  color: #fff;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, .5);
  border-radius: 4px;
  padding: 10px 20px;
  transition: color .8s, text-shadow .8s;
}

.topmenu__item:hover {
  color: #eff0f7;
}

.overlay {
  width: 100%;
  height: 100%;
  z-index: 999;
  background-color: rgba(0, 0, 0, .925);
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

body.menu-open {
  overflow: hidden;
}

.swiper {
  width: 300px;
  height: 220px;
  padding: 5% 0;
}

@media (min-width: 768px) {
  .swiper {
    width: 380px;
    height: 280px;
  }
}

@media (min-width: 1200px) {
  .swiper {
    width: 550px;
    height: 380px;
  }
}

.swiper-slide {
  width: 100%;
  border-radius: 10px;
  position: relative;
  box-shadow: 0 15px 30px rgba(0, 0, 0, .2);
}

.swiper-link {
  letter-spacing: 2px;
  text-align: center;
  color: #fff;
  background: #4654a3;
  border-radius: 20px;
  padding: 7px 18px;
  font-family: inherit;
  font-size: 10px;
  font-weight: 700;
  transition: color .5s ease-in-out, background-color .5s ease-in-out, border-color .5s ease-in-out;
  position: absolute;
  bottom: 1%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: inset 2px -2px 20px rgba(214, 214, 214, .2), inset -3px 3px 3px rgba(255, 255, 255, .4);
}

.swiper-link:hover {
  color: #4654a3;
  background-color: #fff;
  border-color: #4654a3;
}

@media (min-width: 768px) {
  .swiper-link {
    font-size: 14px;
  }
}

