.socials {
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.socials__item {
  width: 44px;
  height: 44px;
}

.socials__icon {
  width: 20px;
  height: 20px;
  fill: #fff;
}

.socials__link {
  width: 100%;
  height: 100%;
  color: #4654a3;
  background-color: #4654a3;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: color .5s ease-in-out, background-color .5s ease-in-out, border-radius .5s ease-in-out, box-shadow .5s ease-in-out;
  display: flex;
}

.socials__link:hover, .socials__link:focus {
  color: #fff;
  background-color: #4654a3;
  border-radius: 30%;
  box-shadow: 0 0 25px rgba(70, 84, 163, .8);
}

.scroll {
  cursor: pointer;
  z-index: 19;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  display: none;
  position: fixed;
  right: 20px;
}

.scroll--up {
  bottom: 60px;
}

@media screen and (min-width: 768px) {
  .scroll--up {
    right: 30px;
  }
}

.scroll--down {
  bottom: 20px;
}

@media screen and (min-width: 768px) {
  .scroll--down {
    right: 30px;
  }
}

.scroll--show {
  display: block;
}

.scroll__icon {
  fill: #4654a3;
  width: 44px;
  height: 44px;
  transition: all .25s cubic-bezier(.8, .4, .3, .9);
}

.scroll__icon:hover, .scroll__icon:focus {
  fill: #000;
  filter: drop-shadow(0 0 5px rgba(70, 84, 163, .8));
}

