}

@media (min-width: 1200px) {
  .hero__thumb {
    width: 300px;
    height: 300px;
  }
}

.hero__thumb:before {
  z-index: -1;
  content: "";
  width: 100%;
  height: 100%;
  background-color: #4654a3;
  border-radius: 50%;
  animation: bg-slide 1.8s ease-out 1.1s both;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
